  #movie {
    width: 80%;
    margin: 100px auto 0 auto;
    .title {
      h1 {
        text-align: center;
      }
      text-align: left;
      p {
        span.upsize {
          font-size: 25px;
          color: royalblue;
        }
      }
    }
    .datatable {
      width: 800px;
      margin: 80px auto;
      position: relative;
      p.label {
        position: absolute;
        top: -17px;
        left: 5px;
        font-weight: bold;
      }
      div.intro {
        background: #f7f7f7;
        padding: 5px 0 5px 5px;
        span.upcolor {
          font-weight: bold;
        }
        p {
          margin: 8px 0;
        }
      }
    }
    .change {
      position: absolute;
      right: 0;
      transform: translateX(-20%);
      top:0 ;
      z-index: 1;
    }
    section.directors {
      font-family: Helvetica, Arial, sans-serif;
      div.toptitle {
        font-size: 28px;
        margin: 0 0 15px 0;
        padding: 15px 0;
        span {
          font-size: 14px;
        }
      }
      div.detail {
        background: #f7f7f7;
        margin-bottom: 20px;
        display: flex;
        .wrap1 {
          img {
            width: 100px;
          }
        }
        .wrap {
          padding-left: 20px;
          height: 142px;
          p {
            margin: 0;
            font-size: 14px;
            margin-bottom: 17px;
            padding-top: 3px;
            .rate {
              font-size: 17px;
              color: #af4849
            }
          }
          p.last {
            margin-bottom: 0;
          }
        }
      }
    }
    section.movietop {
      font-family: Helvetica, Arial, sans-serif;
      div.movietitle {
        font-size: 28px;
        padding-bottom: 12px;
      }
      div.moviedata {
        display: flex;
        flex-wrap: wrap;
        width: 800px;
        .moviedetail {
          padding-right: 43px;
          padding-bottom: 15px;
          p {
            margin: 0;
            font-size: 13px;
            text-align: center;
            width: 125px;
          }
          p.movie_year {
            color: #ccc;
            margin-bottom: 10px
          }
          img {
            width: 125px;
            height: 187px;
          }
          .movie_rate {
            color: #e09015;
          }
        }
        .moviedetail:nth-child(5), .moviedetail:nth-child(10){
          padding-right: 0;
        }
      }
      div.movieintro {
        background: #f7f7f7;
        padding: 5px 0 5px 5px;
        font-size: 13px;
        span.upcolor {
          font-weight: bold;
        }
        p {
          margin: 8px 0;
        }
      }
    }
  }
</style>



