import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import ProxyManage from '@/components/ProxyManage'
import Movie from '@/components/Movie'
import User from '@/components/User'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ProxyManage',
      component: ProxyManage
    },
    {
      path: '/proxy',
      name: 'ProxyManage',
      component: ProxyManage
    },
    {
      path: '/movie',
      name: 'Movie',
      component: Movie
    },
    {
      path: '/user',
      name: 'User',
      component: User
    }
  ]
})



