<template>
  <div id="proxy">
    <!--<el-button @click="changeData">测试</el-button>-->
    <el-container>
      <el-aside width="60%">
        <div id="proxies">
          <el-table
            v-loading="loading"
            element-loading-text="正在获取数据"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)"
            :data="proxies"
            :stripe = true
            style="width: 100%">
            <el-table-column
              label="代理"
              align="center"
              prop="proxy"
              min-width="150">
            </el-table-column>
            <el-table-column
              align="center"
              label="num"
              prop="num"
              min-width="150">
            </el-table-column>
            <el-table-column
              align="center"
              label="来源"
              prop="from"
              min-width="150">
            </el-table-column>
            <el-table-column
              min-width="150"
              align="center"
              label="操作">
              <template slot-scope="scope">
                <el-button
                  size="mini"
                  type="info"
                  @click="handleCopy(scope.$index, scope.row)">复制</el-button>
                <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, proxies)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-aside>
      <el-main>
        <div class="api">
          <div class="header">
            <i class="el-icon-info"></i>
            <span class="apiTitle">IP代理池API设计</span>
          </div>
          <div class="methond">
            <div class="info"> <span class="operate">获取随机代理IP</span> <span class="link">http://localhost:5000/get/</span></div>
            <div class="info"><span class="operate">获取全部代理IP</span> <span class="link">http://localhost:5000/get_all/</span></div>
            <div class="info"><span class="operate">删除指定代理IP</span> <span class="link">http://localhost:5000/delete?proxy=<span class="alert">ip</span></span></div>
            <div class="info"><span class="operate">查看代理池状态</span> <span class="link">http://localhost:5000/get_status/</span></div>
          </div>
          <div class="header">
            <i class="el-icon-loading"></i>
            <span class="apiTitle">IP代理池状态</span>
          </div>
          <div class="wrap">
            <div class="methond">
              <div class="info"><span class="operate">原始代理IP数量</span> <span class="bigsize">{{raw_proxy}}</span></div>
              <div class="info"><span class="operate">可用代理IP数量</span> <span class="bigsize">{{useful_proxy}}</span></div>
            </div>
            <div class="shanku">
              <el-button size="medium" type="danger" @click="centerDialogVisible = true">删库跑路</el-button>
            </div>
          </div>
        </div>
      </el-main>
      <el-dialog
        title="提示"
        :visible.sync="centerDialogVisible"
        width="30%"
        center>
        <span>确定要重置代理池吗？</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="centerDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="shanku">确 定</el-button>
        </span>
      </el-dialog>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'proxy',
  data () {
    return {
      proxies: [],
      loading: false,
      raw_proxy: 0,
      useful_proxy: 0,
      centerDialogVisible: false
    }
  },
  methods: {
    getData () {
      var _this = this
      this.$ajax({
        method: 'get',
        url: 'http://localhost:5000/show_all/'
      }).then(function (res) {
        _this.proxies = res.data
      })
    },
    getStatus () {
      var _this = this
      this.$ajax({
        method: 'get',
        url: 'http://localhost:5000/get_status/'
      }).then(function (res) {
        _this.raw_proxy = res.data['raw_proxy']
        _this.useful_proxy = res.data['useful_proxy']
      })
    },
    handleCopy (index, row) {
      console.log(index, row)
      const input = document.createElement('input')
      document.body.appendChild(input)
      input.setAttribute('value', row.proxy)
      input.select()
      if (document.execCommand('copy')) {
        document.execCommand('copy')
        this.$message({
          showClose: true,
          message: '已复制到剪切板',
          type: 'success'
        })
      }
      document.body.removeChild(input)
    },
    handleDelete: function (index, rows) {
      var _this = this
      var proxy = rows[index]['proxy']
      this.deleteProxy(proxy).then(function (result) {
        if (result === 'success') {
          rows.splice(index, 1)
          _this.$message({
            showClose: true,
            message: '已删除代理 ' + proxy + '',
            type: 'success'
          })
          _this.getStatus()
          console.log(result)
        } else {
          _this.$message({
            showClose: true,
            message: '删除失败 请检查网络api接口是否开启',
            type: 'error'
          })
        }
      })
    },
    changeData () {
    },
    deleteProxy (proxy) {
      // var _this = this
      // requests.get("http://127.0.0.1:5000/delete?proxy={}".format(proxy))
      return new Promise((resolve, reject) => {
        var url = 'http://127.0.0.1:5000/delete?proxy=' + proxy
        this.$ajax({
          url: url
        }).then(function (res) {
          resolve(res.data)
        })
      })
    },
    refreshStatus () {
      var _this = this
      setInterval(_this.getStatus, 10000)
      setInterval(_this.getData, 10000)
    },
    deleteAll () {
      var url = 'http://127.0.0.1:5000/delete_all'
      this.$ajax({
        url: url
      }).then(function (res) {
        console.log('删库成功')
      })
    },
    shanku () {
      this.centerDialogVisible = false
      this.deleteAll()
    }
  },
  mounted () {
    this.getData()
    this.getStatus()
    this.refreshStatus()
  }
}
</script>

<style scoped>
  html,body {
    margin: 0 !important;
    padding: 0 !important;
  }
  div#proxy {
    height: 100%;
    margin-top: 60px;
  }
  div.api {
    margin: 15px auto;
    padding-left: 40px;
    padding-top: 20px;
    padding-bottom: 30px;
    background: rgb(249, 249, 249);
    width: 480px;
    border-radius: 4px;
  }
  div.header {
    margin-bottom: 16px;
  }
  span.apiTitle {
    padding-left: 15px;
  }
  div.methond {
    margin-bottom: 20px;
  }
  div.info {
    display: flex;
    padding: 10px 0;
    color: #8EB4FE;
    align-items: baseline;
  }
  div.info .operate {
    display: inline-block;
    width: 120px;
    padding-right: 10px;
    color: gray;
  }
  span.alert {
    color: red;
  }
  div.luangao {
    padding-top: 40px;
  }
  span.bigsize {
    font-size: 25px;
  }
  .el-main {
    position: relative;
    /*background-image: url("http://owfi274fz.bkt.clouddn.com/18-5-30/10994084.jpg");*/
    background-image: url("http://owfi274fz.bkt.clouddn.com/18-3-21/92092097.jpg");
    background-position: top center;
    min-height: 1000px;
  }
  div.wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  div.shanku {
    margin-right: 50px;
    padding-bottom: 15px;
  }
  section.el-container {
  }
</style>



