<template>
    <div id="navigation">
      <div id="menu-nav">
        <div id="navigation-bar">
          <ul>
            <li><a href="/"><i class="fa fa-home"></i><span>Home</span></a></li>
            <li><a href="#/proxy"><i class="fa fa-cogs"></i><span>Proxy</span></a></li>
            <li><a href="#/movie"><i class="fa fa-user"></i><span>Movie</span></a></li>
            <!--http://localhost:8080/#/movie-->
            <li><a href="#/user"><i class="fa fa-book"></i><span>User</span></a></li>
          </ul>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'Navigation'
}
</script>

<style scoped lang="scss">
  body {
    background: rgb(67,139,244);
    font: normal 300 18px Verdana;
  }

  #navigation {
    position: fixed;
    width: 100%;
    z-index: 2;
    top: 0;
  }
  #menu-nav {
    max-width: 100%;
    margin: 0 auto;
    /*background: #f96e5b;*/
    background: #1f3e54;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  }

  // navigation bar
  #navigation-bar {
    ul {
      margin: 0;
      padding: 0;
      border-radius: 4px;
      overflow: hidden;
      margin: 0;
      max-width: 30%;
      display: flex;
      justify-content: space-around;
    }

    li {
      /*float: left;*/
      /*width: 20%;*/
      text-decoration: none;
      list-style: none;

      &:hover a::before {
        right: 0;
        left: 0;
      }
    }

    a {
      color: #ffffff;
      font-size: 18px;
      text-decoration: none;
      text-align: center;
      padding: 20px 0;
      display: block;
      position: relative;
      transition: all 0.3s ease;

      &:hover {
        color: black;
      }

      &::before {
        content: "";
        transition: all 0.4s ease-in-out;
        position: absolute;
        right: 50%;
        left: 50%;
        bottom: 0;
        height: 2px;
        background-color: #e74c3c;
      }

      &:hover::before {
        right: 0;
        left: 0;
      }
    }

    span {
      display: block;
      font: normal 300 18px Arial;
    }
  }
  form {
    width: 90%;
    margin: 10px auto;
  }
</style>



